import { put, takeEvery } from "redux-saga/effects"

import { CREATE_POST, CREATE_POST_RED, DELETE_POST, DELETE_POST_RED, GET_POST, GET_POST_RED, UPDATE_POST, UPDATE_POST_RED } from "../Constant"
import { createRecordFormData, deleteRecord, getRecord, updateRecord, updateRecordFormData } from "./Services/Services"

function* createSaga(action) {
    let response = yield createRecordFormData("posts", action.payload)
    yield put({ type: CREATE_POST_RED, payload: response })
}

function* getSaga() {
    let response = yield getRecord("posts")
    yield put({ type: GET_POST_RED, payload: response })
}

function* updateSaga(action) {
    yield updateRecordFormData("posts", action.payload)
    yield put({ type: UPDATE_POST_RED, payload: action.payload })
}

function* deleteSaga(action) {
    yield deleteRecord("posts", action.payload)
    yield put({ type: DELETE_POST_RED, payload: action.payload })
}


export default function* postSagas() {
    yield takeEvery(CREATE_POST, createSaga)
    yield takeEvery(GET_POST, getSaga)
    yield takeEvery(UPDATE_POST, updateSaga)
    yield takeEvery(DELETE_POST, deleteSaga)
}