import React, { useEffect, useState } from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import { deletePost, getPost } from '../Store/ActionCreators/PostActionCreators'
import { useDispatch, useSelector } from 'react-redux'

const ViewPost = () => {
  let [result,setResult]=useState([])
let [updateMode,setUpdateMode]=useState(false)
let navigate=useNavigate()
const {_id}=useParams()

let dispatch=useDispatch()


let PostStateData=useSelector((state)=>state.PostStateData)


function getAPIData() {
  dispatch(getPost())
  if (PostStateData?.data?.length)
      setResult(PostStateData?.data)
  else
      setResult([])
}

let finalitem=result.filter((x)=>x._id==_id)

if(finalitem?.length){
  finalitem=finalitem[0]
}

useEffect(() => {
  const userId = localStorage.getItem('userid');
  
  if (userId === finalitem?.username) {
    setUpdateMode(true);
  } else {
    setUpdateMode(false);
  }
}, [finalitem]);


useEffect(()=>{
getAPIData()
},[PostStateData.length])


const navigatetoedit=()=>{
  navigate(`/update-post/${_id}`)
}


function handleDelete(e){
   deletePost(_id)
   navigate('/')
}

  return (
    <div className='singlePost'>
    <div className="singlePostWrapper">
     {/* {post.photo && ( */}
       <img src={`/${finalitem?.pic}`}
        alt="" 
        className="singlePostImg" />
       {/* )} */}
       {
        

     <h1 className="singlePostTitle">
         {finalitem?.title} 
     </h1>
     
     }
     <div className="singlePostInfo">
         <span className="singlePostAuthor">Author:
          <Link to={`/?user=username`} className='link'>
           <b>username</b>
          </Link>
         </span>
         <div className='d-flex'>
           <span className="postDate me-4">{new Date(finalitem?.createdAt).toLocaleDateString()}</span>
           <span className="postDate me-4">{new Date(finalitem?.createdAt).toLocaleTimeString()}</span>
        </div>
     </div>
     
       <p className='singlePostDesc'>
        {finalitem.content}
       </p>
   { updateMode?
        <div className='d-flex justify-content-between ms-3 me-4'>
            <button className="singlePostButton" onClick={navigatetoedit} >Edit</button>
            <button className="singlePostButtonDelete" onClick={handleDelete} >Delete</button>
        </div>:""
   }
    </div>
 </div>
  )
}

export default ViewPost
