import React, { useEffect, useState } from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import { getPost, updatePost } from '../Store/ActionCreators/PostActionCreators'
import { useDispatch, useSelector } from 'react-redux'

const UpdatePost = () => {
  let [result,setResult]=useState([])
  let [data, setData] = useState({
    title: "",
    content: "",
    pic: "",
})

let [updateMode,setUpdateMode]=useState(false)
const {_id}=useParams()

let dispatch=useDispatch()
let navigate=useNavigate()

let PostStateData=useSelector((state)=>state.PostStateData)
// console.log(PostStateData)


function getAPIData() {
  dispatch(getPost())
  if (PostStateData?.data?.length)
      setResult(PostStateData?.data)
  else
      setResult([])
}
// console.log(data,"data")

let finalitem=result.filter((x)=>x._id==_id)

useEffect(()=>{
  if(finalitem?.length){
        finalitem=finalitem[0]
        data.setTitle(finalitem?.title) 
        data.setContent(finalitem?.content) 

  }
},[finalitem?._id])




console.log(typeof(finalitem))

useEffect(()=>{
getAPIData()
},[PostStateData.length])


function getInputData(e) {
    let name = e.target.name
    let value = e.target.files ? e.target.files[0] : e.target.value
    setData((old) => {
        return ({
            ...old,
            [name]: value
        })
    })
}


async function handleUpdatePost(e){
    e.preventDefault()
    let formData = new FormData()
            formData.append("_id",_id)
            formData.append("title",data.title)
            formData.append("content",data.content)
            formData.append("pic",data.pic)
            dispatch(updatePost(formData))
            navigate("/")
  }


  return (
     <div>
        <form onSubmit={handleUpdatePost}>
            <div className="row  d-flex justify-content-center">
                <div className="col-md-9">
                    <label className='fs-3'>Title</label>
                   <input className='form-control' type="text" name="title" id="" value={data.title}  onChange={getInputData}  autoFocus/>                   
                </div>
                <div className="col-md-9 mt-4">
                    <label className='fs-3'>Content</label>
                    <div>
                        <textarea name='content' rows={4}   className='singlePostDescInput form-control border-primaryy border-2' value={data.content} onChange={getInputData} autoFocus></textarea>
                    </div>
                </div>
                <div className="col-md-9 mt-4">
                    <label className='fs-3'>Pic</label>
                   <input className='form-control' type="file" name="pic" id=""  onChange={getInputData}/>                   
                </div>
            </div>
            <div className='d-flex justify-content-end me-7'>             
               <button  type='submit' className='singlePostButton'>Update</button>
            </div>
        </form>
     </div>
  )
}

export default UpdatePost
