import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import formValidators from '../components/Validators/formValidators'
import imageValidators from '../components/Validators/imageValidators'
import { Link, useNavigate } from 'react-router-dom'
import { createPost, getPost } from '../Store/ActionCreators/PostActionCreators'
export default function Create() {

    let dispatch=useDispatch()
    
    let [postData, setPostData] = useState({
        title: "",
        content: "",
        pic:""
    })
    let [show, setShow] = useState(false)
    let [errorMessage, setErrorMessage] = useState({
        title: "Title Field is Mendatory",
        content: "Content Field is Mendatory",
        pic: "Pic Field is Mendatory",
    })
    let navigate = useNavigate()

  let PostStateData=useSelector((state)=>state.PostStateData)


  function getAPIData() {
    dispatch(getPost())
    if (PostStateData?.data?.length)
        setPostData(PostStateData?.data)
    else
     setPostData([])
}

useEffect(()=>{
  getAPIData()
},[PostStateData.length])

    function getInputData(e) {
        let name = e.target.name
        let value = e.target.files ? e.target.files[0] : e.target.value
            setErrorMessage((old) => {
                return {
                    ...old,
                    [name]: e.target.files ? imageValidators(e) : formValidators(e)
                }
            })
        // }
        setPostData((old) => {
            return {
                ...old,
                [name]: value
            }
        })
    }
    function posttData(e) {
        e.preventDefault()
        console.log(errorMessage)
        let error = Object.values(errorMessage).find((x) => x.length > 0)
        if (error)
            setShow(true)
        else {
            let item = PostStateData?.data?.find((x) => x.title.toLowerCase() === postData.title.toLowerCase())
            if (item) {
                setShow(true)
                setErrorMessage((old)=>{
                    return{
                        ...old,
                        title:"Title is already exist"
                    }
                })
            }
            else {
                let formData = new FormData()
                formData.append("title", postData.title)
                formData.append("content", postData.content)
                formData.append("username", localStorage.getItem('userid'))
                formData.append("pic", postData.pic)
                dispatch(createPost(formData))
                navigate("/")
            }
        }
    }


    return (
        <>
            <div className="container-fluid my-3 mt-5">
                <div className="row">
                    <div className="col-md-9 col-sm-8 col-10 m-auto">
                        <h5 className='bg-primaryy p-2 text-light text-center mb-5 w-100'>Create A Post</h5>
                        <form onSubmit={posttData}>
                            <div className="mb-3">
                                <label className='ms-2 fs-5'>Title*</label>
                                <input type="text"  name="title" className='form-control border-primaryy border-2' onChange={getInputData} placeholder='Enter Title' />
                                {show && errorMessage.title ? <p className='text-danger'>{errorMessage.title}</p> : ""}
                            </div>


                            <div className="mb-3">
                            <div>
                             <label className='ms-2 fs-5'>Content*</label>
                            </div>
                            <textarea name='content' rows={5}  className='singlePostDescInput form-control border-primaryy border-2' onChange={getInputData} placeholder='Enter the description of post'></textarea>
                                {show && errorMessage.content ? <p className='text-danger'>{errorMessage.content}</p> : ""}
                            </div>


                            <div className="mb-3">
                                <label className='ms-2 fs-5'>pic*</label>
                                <input type="file" name="pic" className='form-control border-primaryy border-2' onChange={getInputData} placeholder='Select pic' />
                                {show && errorMessage.pic ? <p className='text-danger'>{errorMessage.pic}</p> : ""}
                            </div>

                            <div className="mb-3 text-end">
                                <button type="submit" className='btn btn-primary bg-primaryy'>Create</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    )
}
