import React, { useEffect ,useState} from 'react'
import Header from '../components/header/Header'
import Post from '../components/post/Post'
import {getPost} from '../Store/ActionCreators/PostActionCreators'
import {useDispatch, useSelector} from 'react-redux'

const Home = () => {

  let [data,setData]=useState([])
  let dispatch=useDispatch()

  let PostStateData=useSelector((state)=>state.PostStateData)
  // console.log(PostStateData?.data)

  function getAPIData() {
    dispatch(getPost())
    if (PostStateData?.data?.length)
        setData(PostStateData?.data)
    else
        setData([])
}
// console.log(data,"data")

useEffect(()=>{
  getAPIData()
},[PostStateData.length])

  return (
    <div>
       <Header />
       <div className='home'>
        {
            data?.map((item, index) => {
                return <Post key={index} item={item} />
            })
        }
        {/* <Post data={data} /> */}
        {/* <Post />
        <Post />
        <Post />
        <Post />
        <Post />
        <Post />
        <Post /> */}
        {/* <Sidebar /> */}
      </div>
    </div>
  )
}

export default Home
