import { CREATE_POST, GET_POST, UPDATE_POST, DELETE_POST } from "../Constant";

export function createPost(data){
  return{
    type:CREATE_POST,
    payload:data
  }
}


export function getPost(){
    return{
      type:GET_POST,
    }
  }
  
  export function updatePost(data){
    return{
      type:UPDATE_POST,
      payload:data
    }
  }
  
  export function deletePost(data){
    return{
      type:DELETE_POST,
      payload:data
    }
  }