import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, NavLink, useNavigate } from 'react-router-dom'

const Navbar = () => {
    const navigate=useNavigate()  
    const navigateToCreate=()=>{
      navigate('/create')
    }

  return (
    // <!-- Navbar Start -->
    <div className="container-fluid sticky-top bg-white px-0 wow fadeIn mb-3" data-wow-delay="0.1s">


      <nav className="navbar navbar-expand-lg navbar-light py-lg-0 px-lg-5 wow fadeIn bg-navbar" data-wow-delay="0.1s">
        <Link to="/" className="navbar-brand ms-4 ms-lg-0">
          <h1 className=" text-primary m-0">BlogApp</h1>
        </Link>
        <button type="button" className="navbar-toggler me-4" data-bs-toggle="collapse"
          data-bs-target="#navbarCollapse">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarCollapse">
          <div className="navbar-nav ms-auto p-4 p-lg-0">
            <NavLink to="/" className="nav-item nav-link active">Home</NavLink>
            <NavLink to="/register" className="nav-item nav-link">Register</NavLink>
            <NavLink to="/login" className="nav-item nav-link">Login</NavLink>
            <button onClick={navigateToCreate} className='btn  btn-create ms-3 me-3'>Create</button>
            <NavLink to="/profile" className="nav-item nav-link">Profile</NavLink>
          </div>

        </div>
      </nav>
    </div>
    // <!-- Navbar End -->
  )
}

export default Navbar
