import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
const Post = ({...item}) => {
  const navigate=useNavigate()
   
  const navigateToView=()=>{
    navigate(`/post/${item?.item?._id}`)
  }

  return (
    <div className='mt-5'>
      <div className='post' onClick={navigateToView}>
       <img className='postImg' src={item?.item?.pic} alt="not found" />
       <div className="postInfo">
        <Link to={item?.item?._id} className='link'>
        <span className="postTitle">{item?.item?.title}</span>
        </Link>
        <div className='d-flex'>
         <span className="postDate me-4">{new Date(item?.item?.createdAt).toLocaleDateString()}</span>
         <span className="postDate">{new Date(item?.item?.createdAt).toLocaleTimeString()}</span>
        </div>
       </div>
       <p className='postDesc'>
        {item?.item?.content}
       </p>
    </div>
    </div>
  )
}

export default Post
