import React, { useState } from 'react'
import { Link } from 'react-router-dom'

const Edit = () => {
let [updateMode,setUpdateMode]=useState(true)

  return (
    <div className='singlePost'>
    <div className="singlePostWrapper">
     {/* {post.photo && ( */}
       <img src='/images/bluewater.jpg'
        alt="" 
        className="singlePostImg" />
       {/* )} */}
       {
         updateMode ? <input type="text" value='title' className='singlePostTitleInput' autoFocus 
        /> :(

     <h1 className="singlePostTitle">
         title
         {/* {
           post.username===user?.username && (
             <div className="singlePostEdit">
             <i class="singlePostIcon fa-regular fa-pen-to-square"  
             onClick={()=>setUpdateMode(true)}></i>
             <i class="singlePostIcon fa-solid fa-trash-can" onClick={handleDelete}></i>
             </div>
           )
         } */}
       
     </h1>
      )
     }
     <div className="singlePostInfo">
         <span className="singlePostAuthor">Author:
          <Link to={`/?user=username`} className='link'>
           <b>username</b>
          </Link>
         </span>
         <span className="singlePostDate me-4">12/22/2024</span>
     </div>
     {updateMode ? (
       <textarea className='singlePostDescInput'>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Architecto sapiente repellat tenetur dolore beatae modi quam sit magni, esse accusamus velit nam quia temporibus, error expedita molestias ex incidunt illum.</textarea>
     ) :(
       <p className='singlePostDesc'>
       Lorem ipsum dolor sit amet consectetur adipisicing elit. Doloribus repudiandae atque fuga error minima distinctio. Laboriosam quo quidem facilis, fugit dolorum dignissimos delectus ipsa omnis illum atque similique tempora veniam.
       </p>
     )      
   }
   {
     updateMode && (
        <div className='text-center'>
            <button className="singlePostButton" >Update</button>
        </div>

     )
   }
    </div>
 </div>
  )
}

export default Edit
