import { CREATE_POST_RED, DELETE_POST_RED, GET_POST_RED, UPDATE_POST_RED } from "../Constant"

export default function PostReducer(state=[], action) {
    let newState, index
    switch (action.type) {
        case CREATE_POST_RED:
            newState = [...state]
            newState.push(action.payload)
            return newState

        case GET_POST_RED:
            return action.payload

        case UPDATE_POST_RED:
            console.log(state)
            index = state?.data?.findIndex((x) => x.id === action.payload.id)
            state.data[index].title = action.payload.title
            state.data[index].pic = action.payload.pic
            state.data[index].content = action.payload.content
            return state

        case DELETE_POST_RED:
            return state.filter((x) => x.id !== action.payload.id)
        default:
            return state
    }
}